import { View } from '../router/View.js';

export class HelpView implements View {
    public id = 'help';

    public init(container: HTMLElement): void {
        const template = document.getElementById('helpTemplate') as HTMLTemplateElement;

        if (template) {
            const content = template.content.cloneNode(true);
            container.appendChild(content);
        } else {
            console.error('Help template not found!');
            container.innerHTML = '<p>Error loading help view</p>';
        }
    }

    public onEnter(): void {
        document.title = 'Help - Bike Power Tracker';
    }

    public onLeave(): void {
        // Reset title if needed, but usually the next view sets it
    }
}
